import {
    MDBBtn,
    MDBContainer,
    MDBCard,
    MDBCardBody,
    MDBInput,
}
    from 'mdb-react-ui-kit';


function PrivacyPolicy() {
    return (
        <div>

            <MDBContainer fluid>

                <div className=" bg-image text-center" style={{ backgroundImage: 'url(https://mdbootstrap.com/img/new/textures/full/171.jpg)', padding: '40px 80px 140px' }}>
                    <h1 style={{ color: 'whitesmoke' }}> Privacy Policy</h1>
                    <h3 style={{ color: 'whitesmoke' }}></h3>
                </div>

                <MDBCard className='mx-5 mb-5 p-5 shadow-5' style={{ marginTop: '-100px', background: 'hsla(0, 0%, 100%, 0.8)', backdropFilter: 'blur(30px)' }}>
                    <MDBCardBody className='p-5 '>
                        <h3 className="fw-semi-bold" style={{ fontSize: '35px', padding: '5px 0' }}>1. Information We Collect:</h3>
                        <p>We may collect various types of information when you interact with our website, including but not limited to:</p>
                        <ul>
                            <li> Personal Information: Name, address, phone number, email address, and other contact details.</li>
                            <li> Billing and Payment Information: Credit card or other payment details.</li>
                            <li> Account Information: Username and password for your account.</li>
                            <li> Purchase Information: Products you browse, add to your cart, or purchase, as well as your purchase history.</li>
                            <li> Device and Usage Information: Information about the device you use to access our website, including IP address, browser type, and operating system, as well as how you interact with our website.</li>
                            <li> Communication Information: Records of your interactions with our customer support or through other means of communication.</li>
                            <li> Cookies and Similar Technologies: Information collected through cookies and similar technologies to improve your experience on our website.</li>
                        </ul>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>2. How We Use Your Information:</h3>
                        <p>We use your information for various purposes, including:</p>
                        <ul>
                            <li> Processing Orders: To process and fulfill your orders for products, including payment processing and shipping.</li>
                            <li> Account Management: To manage your account, including providing order status updates and facilitating customer support.</li>
                            <li> Personalization: To personalize your experience and offer product recommendations.</li>
                            <li> Marketing and Promotions: To inform you about special offers, promotions, and new products, provided you've opted in to receive such communications.</li>
                            <li> Analytics: To analyze user behavior, improve our website, and enhance our services.</li>
                            <li> Legal and Security: To comply with legal obligations and protect the security and integrity of our website.</li>
                        </ul>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>3. How We Share Your Information:</h3>
                        <p>We may share your information with third parties under the following circumstances:</p>
                        <ul>
                            <li> Service Providers: We may share your data with third-party service providers to help us with payment processing, shipping, marketing, and analytics.</li>
                            <li> Legal Compliance: We may disclose your information when required by law or to protect our rights, safety, and property.</li>
                        </ul>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>4. Your Choices and Rights:</h3>
                        <p>You have certain rights regarding your personal information, including the right to access, correct, or delete your data. You can also opt-out of marketing communications at any time. Please contact us at [contact email] to exercise these rights or for any privacy-related inquiries.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>5. Security:</h3>
                        <p>We take security seriously and use reasonable administrative, technical, and physical safeguards to protect your information from unauthorized access, disclosure, alteration, or destruction.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>6. Cookies:</h3>
                        <p>We use cookies and similar technologies to enhance your browsing experience. You can manage your cookie preferences by adjusting your browser settings.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>7. Children's Privacy:</h3>
                        <p>Our website and services are not intended for children under 13 years of age. We do not knowingly collect information from children under 13. If you believe we have inadvertently collected such information, please contact us to have it removed.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>8. Changes to this Policy:</h3>
                        <p>We may update this Privacy Policy to reflect changes in our practices or for other operational, legal, or regulatory reasons. We will provide notice of any material changes on our website.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>9. Contact Us:</h3>
                        <p>If you have any questions, concerns, or requests regarding this Privacy Policy, please contact us at:</p>
                        <p>Elegances</p>
                        <p className='mt-5'>📧<a style={{ marginRight: '10px' }} href='mailto:jackandlook@myyahoo.com'>
                            jackandlook@myyahoo.com
                        </a>|☎ <a href='tel:7440819665'>
                                +91-7440819665
                            </a></p>

                        Thank you for trusting Elegances with your personal information. We are committed to maintaining your privacy and providing you with a secure and enjoyable shopping experience.
                    </MDBCardBody>
                </MDBCard>

            </MDBContainer>
        </div>

    )
}

export default PrivacyPolicy