import React, { useEffect, useState } from 'react';
import './Receipt.css';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';

const Receipt = () => {
    const { orderId } = useParams();
    const [totalData, setTotalData] = useState()

    useEffect(() => {
        axios.get(`/receipt-by-id/${orderId}`).then((res) => {
            setTotalData(res.data)
        })
    },[])

    function receiptTotal(products) {
        var sum = 0;
        products.forEach(product => sum += (product.price * product.qty))
        return sum
    }

    if (!totalData) {
        return <h1>Loading please wait...</h1>
    }

    return (
        <div className="container">
            <div className="header">
                <h1>Elegances</h1>
                <p>Nasoli, Jalor, Rajasthan, 343029</p>
                <p>jackandlook@myyahoo.com | +91-7440819665</p>
            </div>
            <div className="receipt-info">
                <p><strong>Order ID:</strong> { orderId}</p>
                <p><strong>Date:</strong> { totalData.date}</p>
            </div>
            <div className="customer-info">
                <h3>Customer Details:</h3>
                <p><strong>Name:</strong> {totalData.name}</p>
                <p><strong>Email:</strong> {totalData.email}</p>
                <p><strong>Address:</strong> {totalData.address}</p>
                <p><strong>Payment mode:</strong>Online</p>
                <p><strong>Payment ID: </strong> {totalData.paymentId}</p>
            </div>
            
            <table>
                <thead>
                    <tr>
                        <th>Item</th>
                        <th>Price</th>
                        <th>Quantity</th>
                        <th>Total</th>
                    </tr>
                </thead>
                <tbody>
                    {totalData.products.map((product) => {
                        return <tr>
                            <td>{ product.item}</td>
                            <td>₹{product.price}</td>
                            <td>{ product.qty}</td>
                            <td>₹{ product.price * product.qty}</td>
                        </tr>
                    })}
                </tbody>
            </table>
            <div className="total">
                <p><strong>Total Amount:</strong> ₹{ receiptTotal(totalData.products)}</p>
            </div>
            <button onClick={handlePrint}>Print Receipt</button>
        </div>
    );
};

const handlePrint = () => {
    window.print();
};

export default Receipt;
