import React from 'react'
import {
    MDBContainer,
    MDBCard,
    MDBCardBody,
}
    from 'mdb-react-ui-kit';


const ShippingPage = () => {
    return (
        <div>

            <MDBContainer fluid>

                <div className=" bg-image text-center" style={{ backgroundImage: 'url(https://mdbootstrap.com/img/new/textures/full/171.jpg)', padding: '40px 80px 140px' }}>
                    <h1 style={{ color: 'whitesmoke' }}>Shipping Policy</h1>
                    <h3 style={{ color: 'whitesmoke' }}></h3>
                </div>

                <MDBCard className='mx-5 mb-5 p-5 shadow-5' style={{ marginTop: '-100px', background: 'hsla(0, 0%, 100%, 0.8)', backdropFilter: 'blur(30px)' }}>
                    <MDBCardBody className='p-5 '>
                        <p>Welcome to Elegances! This Shipping Policy outlines the terms and conditions for the shipment and pickup of orders placed on our website. By using our services, you agree to comply with and be bound by this Shipping Policy.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '30px', padding: '5px 0' }}>Order Processing Time</h3>

                        <p><strong>Maximum Processing Time:</strong> We strive to process all orders within 1 business day of receiving payment. Orders placed on weekends or holidays will be processed on the next business day.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '30px', padding: '5px 0' }}>Shipping Options</h3>

                        <p>Customers have the following shipping options:</p>

                        <ul>
                            <li><strong>Delivery:</strong> We offer delivery with a minimum shipping time of 2 days and a maximum shipping time of 5 days. Delivery charges may apply and will be calculated at checkout. Please note that shipping times may vary depending on your location and any unforeseen circumstances such as weather conditions or carrier delays.</li>
                            <li><strong>Pickup:</strong> Customers can also choose to pick up their orders from our designated pickup location i.e. <strong>Nasoli, Jalor, Rajasthan, 343029</strong> . Orders must be picked up within 5 days of receiving the pickup notification. Pickup hours are from 9am to 7pm. Please bring a valid ID and your order confirmation email for pickup.</li>
                        </ul>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '30px', padding: '5px 0' }}>Shipping Rates</h3>

                        <p>Shipping rates are calculated based on the weight and destination of the package. You can view the shipping charges for your order during the checkout process. Please note that additional fees may apply for expedited shipping or international deliveries.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '30px', padding: '5px 0' }}>Tracking Your Order</h3>

                        <p>Once your order has been shipped, you will receive a tracking number via email. You can use this tracking number to monitor the status of your delivery. If you have any questions about the status of your order, please contact our customer support team for assistance.</p>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '30px', padding: '5px 0' }}>Contact Us</h3>
                        <p>If you have any questions or concerns regarding our Shipping Policy, please contact us at <a style={{ textDecoration: 'none' }} href='mailto:jackandlook@myyahoo.com'>
                            jackandlook@myyahoo.com
                        </a>. Our customer support team is available to assist you during our business hours.</p>
                        <p>Thank you for shopping with Elegances! We appreciate your business and strive to provide you with a seamless shopping experience from start to finish.</p>
                    </MDBCardBody>
                </MDBCard>
            </MDBContainer>
        </div>
    )
}

export default ShippingPage