import React, { useEffect, useState } from 'react';
import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import Home from './pages/Home';
import ProductPage from './pages/ProductPage';
import Header from './components/Header';
import Footer from './components/Footer';
import CategoryPage from './pages/CategoryPage';
import AdminPanel from './pages/AdminPanel';
import LoginPage from './components/Login';
import axios from 'axios';
import CartPage from './pages/Cart';
import Payment from './components/Payment';
import Completion from './components/Completion';
import AboutUs from './pages/AboutUs';
import RefundPolicy from './pages/refundPolicy';
import TermsOfUse from './pages/TermsOfUse';
import Contact from './components/Contact';
import PrivacyPolicy from './pages/privacyPolicy';
import Success from './components/payStatus/Success';
import Failure from './components/payStatus/Failure';
import ShippingPage from './pages/shipping';
import Receipt from './pages/ReceiptPreview';

function App() {
  const [data, setData] = useState([]);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [username, setUsername] = useState(null);
  const [disclaimer, setDisclaimer] = useState(true)

  useEffect(() => {
    axios.get('/productDetails').then((response) => {
      setData(response.data)
    })

  },[])

  return (
    <Router>
      <Header isLoggedIn={isLoggedIn} username={username} />
      <Routes>

        <Route path="/" element={<Home data={data} isLoggedIn={isLoggedIn} setUsername={setUsername} disclaimer={disclaimer} setDisclaimer={setDisclaimer} />} />
        <Route path="/admin" element={<AdminPanel />} />
        <Route path="/about-us" element={<AboutUs />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/shipping-policy" element={<ShippingPage />} />
        <Route path="/receipt-preview/:orderId" element={<Receipt />} />
        <Route path="/payment-status/success" element={<Success />} />
        <Route path="/payment-status/failed" element={<Failure />} />
        <Route path="/terms-of-use" element={<TermsOfUse />} />
        <Route path="/refund-policy" element={<RefundPolicy />} />
        <Route path="/privacy-policy" element={<PrivacyPolicy />} />
        <Route path="/cart" element={<CartPage username={username} data={data} />} />
        <Route path="/login" element={<LoginPage head={"Login"} setIsLoggedIn={setIsLoggedIn} setUsername={setUsername} />} />
        <Route path="/signup" element={<LoginPage head={"SignUp"} />} />
        <Route path="/products/:id" element={<ProductPage data={data} username={username} />} />
        <Route path="/category/:category" element={<CategoryPage data={data} />} />
        <Route path="/checkout/:amount" element={<Payment username={username} />} />
        <Route path="/completion/:amount" element={<Completion username={username} />} />
        

      </Routes>
      <Footer />

    </Router>
  );
}

export default App;
