import {
    MDBBtn,
    MDBContainer,
    MDBCard,
    MDBCardBody,
    MDBInput,
}
    from 'mdb-react-ui-kit';

function AboutUs() {
    return (
        <div>

            <MDBContainer fluid>

                <div className=" bg-image text-center" style={{ backgroundImage: 'url(https://mdbootstrap.com/img/new/textures/full/171.jpg)', padding: '40px 80px 140px' }}>
                    <h1 style={{ color: 'whitesmoke' }}>  Welcome to Elegances – Where Fashion Meets Convenience!</h1>
                    <p className='mt-3' style={{ color: 'whitesmoke', fontSize: '20px' }}>At Elegances, we believe that style and convenience should go hand in hand. Our journey began with a simple vision: to provide fashion enthusiasts with a seamless and enjoyable shopping experience from the comfort of their homes.</p>
                </div>

                <MDBCard className='mx-5 mb-5 p-5 shadow-5' style={{ marginTop: '-100px', background: 'hsla(0, 0%, 100%, 0.8)', backdropFilter: 'blur(30px)' }}>
                    <MDBCardBody className='p-5 '>
                        <h3 className="fw-semi-bold" style={{ fontSize: '35px', padding: '5px 0' }}>What Sets Us Apart</h3>

                        <ol>
                            <li><strong>Quality Assurance:</strong> We understand the importance of quality when it comes to clothing. That's why we carefully select our suppliers and rigorously inspect each item before it reaches your doorstep. Your satisfaction is our top priority.</li>
                            <li><strong>Trendy Selection:</strong> Our team of fashion enthusiasts keeps a close eye on the latest trends, ensuring that our catalog is always up-to-date with the hottest styles. We want you to look and feel your best every day.</li>
                            <li><strong>User-Friendly Shopping:</strong> We've designed our website with you in mind. It's easy to navigate, search for products, and make secure purchases. Your shopping experience should be as enjoyable as wearing our clothes.</li>
                            <li><strong>Customer Support:</strong> Have a question or need assistance? Our friendly and knowledgeable customer support team is here to help. We're just a click or a call away, ready to assist you with any inquiries or concerns.</li>
                            <li><strong>Fast and Reliable Shipping:</strong> We know waiting for your new clothes can be exciting. That's why we work tirelessly to ensure your orders are processed quickly and delivered to you in the shortest possible time.</li>
                        </ol>

                        <h3 className="fw-semi-bold mt-5" style={{ fontSize: '35px', padding: '5px 0' }}>Our Commitment</h3>

                        <p>At Elegances, we're not just selling clothes; we're offering you the opportunity to express yourself, boost your confidence, and embrace your unique style. We believe that everyone deserves to look and feel amazing, no matter their budget or location.</p>

                        <p>Join us on this fashion-forward journey, and let's make every day a stylish one together. Thank you for choosing Elegances as your fashion destination.</p>

                        <p>If you have any questions, suggestions, or feedback, please don't hesitate to get in touch. We love hearing from our valued customers.</p>

                        <p>Happy shopping!</p>

                        <p>Elegances Team</p>
                    </MDBCardBody>
                </MDBCard>

            </MDBContainer>
        </div>
    )
}

export default AboutUs