import { PaymentElement } from "@stripe/react-stripe-js";
import { useState } from "react";
import { useStripe, useElements } from "@stripe/react-stripe-js";
import Paypal from "./Paypal";
import RazorPay from "./RazorPayPayment";
import SabPaisaMain from "../pages/SabPaisaMain";
import { cashfreePay } from "./cashfree/request";

// import Cashfree from "./Cashfree";
// import CardPayment from "./CashfreeDrop";

export default function CheckoutForm({ amount, username }) {
  const stripe = useStripe();
  const elements = useElements();

  const [message, setMessage] = useState(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [viaPaypal, setViaPaypal] = useState(false);
  const [viaCashfree, setViaCashfree] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!stripe || !elements) {
      return;
    }

    setIsProcessing(true);

    const { error } = await stripe.confirmPayment({
      elements,
      confirmParams: {
        // Make sure to change this to your payment completion page
        return_url: `${window.location.origin}/completion/${amount}`,
      },
    });

    if (error.type === "card_error" || error.type === "validation_error") {
      setMessage(error.message);
    } else {
      setMessage("An unexpected error occured.")
    }

    setIsProcessing(false);
  };

  if (viaPaypal) {
    return <Paypal amount={amount} />
  }

  return (
    <>
      <button className="pay-btn" onClick={() => {
        cashfreePay(amount, username)
      }}>
        Pay via Cashfree
      </button>
      {/* Show any error or success messages */}
    </>
  );
}
