import axios from 'axios';
import { cashfree } from './utils'

let version = cashfree.version();

const handlePayment = (sessionId)=>{
    let checkoutOptions = {
        paymentSessionId: sessionId,
        returnUrl: "/cashfree-status/{order_id}",
    }   
    cashfree.checkout(checkoutOptions).then(function(result){
        if(result.error){
            alert(result.error.message);
        }
        if(result.redirect){
            console.log("Redirection")
            console.log(result);
        }
        console.log(result.data);
    });
}

export const cashfreePay = (amount, username)=>{
    axios.post('/cashfreepay', {
        version,
        amount,
        name: "Unknown"
    })
    .then((res)=>{
        // setSessionId(res.data);
        console.log(res.data);
        handlePayment(res.data);
        // window.open(res.data, '_blank');
    })
    .catch((err)=>{
        console.log(err);
    })
}