import React, { useState } from 'react';
import {
    MDBBtn,
    MDBContainer,
    MDBCard,
    MDBCardBody,
    MDBInput,
}
    from 'mdb-react-ui-kit';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function Contact() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');
    const navigate = useNavigate();

    function handleMessage() {
        axios.post('/sendEmail', {
            name: name,
            email: email,
            message: message
        }).then((res) => {
            console.log(res.status);
            if (res.status === 200) {
                // setIsLoggedIn(true);
                navigate('/')
                // setUsername(name)

            } else {
                alert("An error occured in contacting")
                // setEmail('');
                // setName('')
            }
        })
    }


    return (
        <MDBContainer fluid>

            <div className="p-5 bg-image" style={{ backgroundImage: 'url(https://mdbootstrap.com/img/new/textures/full/171.jpg)', height: '300px' }}></div>

            <MDBCard className='mx-5 mb-5 p-5 shadow-5' style={{ marginTop: '-100px', background: 'hsla(0, 0%, 100%, 0.8)', backdropFilter: 'blur(30px)' }}>
                <MDBCardBody className='p-5 text-center'>

                    <h2 className="fw-bold">Contact Us</h2>
                    <p className='mb-3'>📧<a style={{ marginRight: '10px' }} href='mailto:jackandlook@myyahoo.com'>
                    jackandlook@myyahoo.com
                    </a>|☎ <a href='tel:7440819665'>
                            +91-7440819665
                        </a></p>
                    <p className='mb-5'>
                        <strong>Operating Address: </strong>Nasoli, Jalor, Rajasthan, 343029
                    </p>

                    <MDBInput wrapperClass='mb-4' style={{ marginLeft: 'auto', marginRight: 'auto', maxWidth: '300px' }} label='Name' id='form1' type='text' onChange={(e) => { setName(e.target.value) }} value={name} />
                    <MDBInput wrapperClass='mb-4' style={{ marginLeft: 'auto', marginRight: 'auto', maxWidth: '300px' }} label='Email' id='form1' type='password' onChange={(e) => { setEmail(e.target.value) }} value={email} />
                    <div align="center">
                        <div className="input-group" style={{ maxWidth: '300px' }}>
                            <div className="input-group-prepend">
                                <span className="input-group-text" id="basic-addon" style={{ height: '100%' }}>
                                    <svg fill="#000000" version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink"
                                        width="20px" viewBox="0 0 528.899 528.899"
                                    >
                                        <g>
                                            <path d="M328.883,89.125l107.59,107.589l-272.34,272.34L56.604,361.465L328.883,89.125z M518.113,63.177l-47.981-47.981
		c-18.543-18.543-48.653-18.543-67.259,0l-45.961,45.961l107.59,107.59l53.611-53.611
		C532.495,100.753,532.495,77.559,518.113,63.177z M0.3,512.69c-1.958,8.812,5.998,16.708,14.811,14.565l119.891-29.069
		L27.473,390.597L0.3,512.69z"/>
                                        </g>
                                    </svg>
                                </span>
                            </div>
                            <textarea
                                className="form-control"
                                id="exampleFormControlTextarea1"
                                rows="5"
                                onChange={(e) => { setMessage(e.target.value) }}
                                value={message}
                            ></textarea>

                        </div>
                    </div>
                    <p>Message</p>

                    <MDBBtn className='mb-4' size='md' onClick={() => { handleMessage() }} >Send Message</MDBBtn>



                </MDBCardBody>
            </MDBCard>

        </MDBContainer>
    );
}

export default Contact;